import React, { Component } from 'react'

export default class Consult2 extends Component {
  constructor(props){
    super(props) ;
    this.state = {
        objects : [],
        count : 0
    }
}
addObject = (data) => {
    let a  = this.state.objects ;
    let b = data.map( el => Object.entries(el))
    a.push(b)
    this.setState({
        objects : a
    })
}
componentDidMount(){
  setTimeout(() => {
    this.addObject(this.props.data) ;
    const data2 = this.props.data.map( el => Object.entries(el))
    data2.map( el => (
    el.map( el => (
      typeof(el[1]) == "object" && this.addObject([el[1]])
    ))
  ))  
  }, 3500);
}
render() {
  const data1 = this.props.data.map(elem => Object.entries(elem)) ;
  return (
    <div>
      <h3>{this.props.fieldsTitle}</h3>
      <ul className='ulConsult'>
        {this.state.objects.map( (m) => (
          m.map( (n,i) => (
          <li key={i}>{n.map( (el) => (
            typeof(el[1]) != "object" && el[1] + "ㅤㅤ"
          ))}</li>
          ))))}
      </ul>
    </div>
  )
}
}
