import React, { Component } from 'react'
import axios from "axios" ;

export default class Login extends Component {
  in = () => {
    let us = document.querySelector("#us").value ;
    let pw = document.querySelector("#pw").value ;
    var data = new FormData() ;
    data.append('username', us);
    data.append('password', pw);
    console.log(data);
    axios({
      headers: { 
        'Content-Type': 'multipart/form-data',
        'Access-Control-Allow-Origin':"*"
      },
      method: 'post',
      url: `http://localhost:8080/login`,
      data : data
    });
  }
  render() {
    return (
      <div>
        <form name='dentistDataForm'>
          <h1>INGRESAR </h1>
          <div>
            <label htmlFor = "us">USUARIO: </label>
            <input type="text" name='us' id='us' />
            <label htmlFor = "pw">CONTRASEÑA: </label>
            <input type="text" name='pw' id='pw' />
          </div>
        </form>
        <div>
          <button className='button' onClick= {this.in}>INGRESAR</button>
        </div>
      </div>
    )
  }
}
