import React, { Component } from 'react'
import Delete from '../Mappings/Delete';
import In from '../Mappings/In';
import axios from "axios" ;
import Consult2 from '../Mappings/Consult2';

export default class Turn extends Component {
  constructor(props){
    super(props);
    this.state = {
      component : 1,
      localHost : "http://localhost:8080/turn" ,
      data : []
    }
  }
  componentChange = (event) => {
    if(event.target.id == 1 ){
      this.setState({
        component : 1
      })
    }
    if(event.target.id == 2 ){
      this.setState({
        component : 2 
      })
    }
    if(event.target.id == 3 ){
      this.setState({
        component : 3
      })
    }
  }
  componentDidMount(){
    setTimeout(() => {
      this.getData()
    }, 3000);
  }
  getData = async() => {
    try{
      let response = await axios(`${this.state.localHost}`)
      this.setState({
          data : response.data
      })
    }catch(error){
      console.log(error);
    }
  }
  render() {
    return (
      <div>
        <h1>TURNOS</h1>
        <div>
          <button className='button' id='1' onClick={this.componentChange}>Consultar</button>
          <button className='button' id='2'onClick={this.componentChange}>Ingresar</button>
          <button className='button' id='3'onClick={this.componentChange}>Borrar</button>
        </div>
          {this.state.component == 1 && <Consult2 data = {this.state.data} fieldsTitle = {"IDㅤㅤFECHAㅤㅤPACIENTEㅤㅤODONTOLOGO"}/>}
          {this.state.component == 2 && <In fields = {["date","$","id","$","id"]} fieldsForms = {["Fecha","$","PacienteID","$","DentistaID"]} tableToArm = {["patient","dentist"]} localHost = {this.state.localHost} table = "Paciente"/>}
          {this.state.component == 3 && <Delete localHost = {this.state.localHost} table = "PACIENTE"/>}
      </div>
    )
  }
}
