import React, { Component } from 'react'
import axios from "axios" ;
import Consult2 from '../Mappings/Consult2';

export default class Address extends Component {
  constructor(props){
    super(props) ;
    this.state = {
      localHost : "http://localhost:8080/address" ,
      data : []
    }
  }
  componentDidMount(){
    setTimeout(() => {
      this.getData()
    }, 3000);
  }
  getData = async() => {
    try{
      let response = await axios(`${this.state.localHost}`)
      this.setState({
          data : response.data
      })
    }catch(error){
      console.log(error);
    }
  }
  render() {
    return (
      <div>
        {<Consult2 data = {this.state.data} fields = {["address","number","city"]} fieldsTitle = {["Dirección","Numero","Ciudad"]}/>}
      </div>
    )
  }
}
