import React, { Component } from 'react'
import axios from "axios" ;
export default class Delete extends Component {

    delete = () => {
        let id = document.querySelector("#id").value ;
        axios({
          method: 'delete',
          url: `${this.props.localHost}/delete/${id}`,
        });
      }
  render() {
    return (
      <div>
          <form name='dentistDataForm'>
          <h1>BORRAR {this.props.table}</h1>
          <div>
            <label htmlFor = "id">ID: </label>
            <input type="text" name='id' id='id' />
          </div>
        </form>
        <div>
          <button className='button' onClick= {this.delete}>BORRAR</button>
          <button className='button' id='0' onClick={this.props.fun}>Volver al Home</button>
        </div>
      </div>
    )
  }
}
