import React, { Component } from 'react'
import axios from "axios" ;

export default class In extends Component {
    addDentist = () => {
        let object = {}
        let objectToAdd = [{}]
        let cont = -1 ;
        let change = false
        this.props.fields.map( m => (
            (m == "$") && (change = true , cont ++,objectToAdd.push({})),
            change && ( m != "$" ) && (objectToAdd[cont][m] = document.querySelector("#" + m).value),
            (!change) && (object[m] = document.querySelector("#" + m).value),
            change && (this.props.tableToArm.map( (m,i) => object[m] = objectToAdd[i])) 
        ))
        console.log(object);
        axios({
            method: 'post',
            url: `${this.props.localHost}/add`,
            data: object
        });
        document.querySelector("#form").reset()
    }
    render() {
    return (
        <div>
            <form name='form' id='form'>
            <h1>AGREGAR {this.props.table}</h1>
            {this.props.fields.map( (m,i) => (
                ( m != "$" && 
                    <div key={m+i}>
                        <label htmlFor = {m}>{this.props.fieldsForms[i]}: </label>
                        <input type="text" name={m} id={m} />
                    </div>
                )))}
        </form>
        <div>
          <button className='button' onClick= {this.addDentist}>AGREEGAR</button>
          <button className='button' id='0' onClick={this.props.fun}>Volver al Home</button>
        </div>
      </div>
    )
  }
}
