import React, { Component } from 'react'
import Delete from '../Mappings/Delete';
import In from '../Mappings/In';
import axios from "axios" ;
import Consult2 from '../Mappings/Consult2';

export default class Dentist extends Component {
  constructor(props){
    super(props);
    this.state = {
      component : 1,
      localHost:"http://localhost:8080/dentist",
      data:[]
    }
  }
  componentChange = (event) => {
    if(event.target.id == 1 ){
      this.setState({
        component : 1
      })
    }
    if(event.target.id == 2 ){
      this.setState({
        component : 2 
      })
    }
    if(event.target.id == 3 ){
      this.setState({
        component : 3
      })
    }
  }

  componentDidMount(){
    setTimeout(() => {
      this.getDataDentist()
    }, 3000);
  }
  componentDidUpdate(){
    setTimeout(() => {
      this.getDataDentist()
    }, 9000);
  }

  getDataDentist = async() => {
    try{
      let response = await axios("http://localhost:8080/dentist")
      this.setState({
          data : response.data
      })
    }catch(error){
      console.log(error);
    }
  }

  render() {
    return (
      <div>
        <h1>ODONTOLOGOS</h1>
        <div>
          <button className='button' id='1' onClick={this.componentChange}>Consultar</button>
          <button className='button' id='2'onClick={this.componentChange}>Ingresar</button>
          <button className='button' id='3'onClick={this.componentChange}>Borrar</button>
        </div>
          {this.state.component == 1 && <Consult2 data = {this.state.data} fieldsTitle = {"ID_NOMBRE_____APELLIDO_____MATRICULA"} fields = {["name","lastName","registration"]}/>}
          {this.state.component == 2 && <In fields = {["name","lastName","registration"]} fieldsForms = {["nombre","apellido","matricula"]} localHost = {this.state.localHost} table = {"Odontologo"} tableToArm = {"dentist"} />}
          {this.state.component == 3 && <Delete localHost = {this.state.localHost} table = "DENTISTA"/>}
      </div>
    )
  }
}
